.giftCardPreview {
  img {
    height: auto;
    width: 100%;
  }
  &Big {
    overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap;
    img {
      height: 400px;
      width: auto;
    }
  }
}
.program {
  &Item {
    background: #f4f4f4;
    margin-bottom: 2rem;
  }
}

.detail {
  &Text {
    display: grid;
    gap: 2rem 0;
    margin-bottom: 2rem;
  }
}

.image {
  &Info {
    &Item {
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      align-items: center;
    }
    &Img {
      display: flex;
      width: 5rem;
      justify-content: flex-end;
    }
    &Text {
      margin-left: 2rem;
    }
  }
}
