.containerNoTop {
  padding: 0 20px 20px 20px;
}

.date {
  &Item {
    font-family: "Roboto", sans-serif;
  }
  &Title {
    display: flex;
    font-size: 14px;
    justify-content: space-between;
  }
  &Weekday {
    text-transform: capitalize;
  }
  &Day {
    color: #8e8e8e;
  }
  &TimeList {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 6px 5px;
    margin: 5px 0 20px;
  }
  &Time {
    width: 100%;
    background-color: #fff;
    height: 25px;
    border: 1px solid #b1b1b1;
    border-radius: 3px;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    font-weight: 400;
    color: #8e8e8e;
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
    cursor: pointer;
    &:hover {
      background: #000000;
      color: #fff;
    }
    &Disabled {
      pointer-events: none;
      text-decoration: none;
      opacity: 0.2;
    }
  }
}
