.container {
  padding: 2rem;
  &NoTop {
    padding: 0 2rem 2rem 2rem;
  }
}

.contact {
  &Data {
    input {
      width: 100%;
      height: 41px;
      padding-left: 2rem;
      border-radius: 1rem;
      outline: none;
      border: none;
      background-color: #f4f4f4;
      margin-bottom: 9px;
    }
  }
}

.checkbox {
  display: flex;
  font-size: 1.4rem;
  &:hover {
    button {
      border-color: #ffe600;
    }
  }
  button {
    content: "";
    display: inline-block;
    width: 1.5rem;
    height: 1.5rem;
    flex-shrink: 0;
    flex-grow: 0;
    border: 1px solid #b1b1b1;
    border-radius: 3px;
    margin-right: 1rem;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50% 50%;
    background-color: transparent;
    position: relative;
    &:focus {
      border-color: #ffe600;
    }
    img {
      position: absolute;
      left: 0.25rem;
      top: 0.25rem;
    }
  }
  .buttonActive {
    border-color: #ffd600;
    background: no-repeat center, linear-gradient(180deg, #ffe600, #ffd600);
  }
}

.order {
  &Info {
    &Wrapper {
      background: #f4f4f4;
    }
    &Inner {
      display: grid;
      gap: 2rem 0;
      font-size: 1.2rem;
    }
    &Title {
      font-size: 1.4rem;
      font-weight: 700;
      line-height: 1.65rem;
      color: #141414;
    }
    &Subtitle {
      font-weight: 700;
    }
    &BtnLink span {
      display: inline;
      text-align: left;
      color: #2200a9;
      background: linear-gradient(90deg, #2200a9 50%, transparent 50%) left
        bottom / 10px 1px repeat-x;
      padding-bottom: 3px;
      padding: 0 0 3px 0;
      border: none;
      font-weight: 400;
      line-height: 1.55;
      text-decoration: none;
      vertical-align: middle;
      cursor: pointer;
      border-radius: 0.25rem;
      transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
        border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
    }
    &Program {
      display: grid;
      gap: 1rem 0;
    }
  }
}
