li {
  list-style: none;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  margin: 0 0 1.6rem;
  &.li_none::before {
    opacity: 0;
  }
  &::before {
    counter-increment: myCounter;
    content: counter(myCounter);
    display: inline-block;
    text-align: center;
    margin: 0 1.3rem 0 0;
    font-weight: 700;
  }
}
