html {
  font-size: 10px;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

*::-webkit-scrollbar,
html *::-webkit-scrollbar {
  height: 10px;
  width: 10px;
}

*::-webkit-scrollbar-thumb,
html *::-webkit-scrollbar-thumb {
  background-color: lightgrey;
  border-radius: 5px;
  border: 3px solid white;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

input,
select {
  vertical-align: middle;
}

input[type="text" i] {
  padding: 1px 2px;
}

p {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

ol {
  counter-reset: myCounter;
  font-size: 1.4rem;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

textarea {
  overflow: auto;
}

body {
  font-family: "Roboto", sans-serif;
  margin: 0;
  min-height: 100%;
  background-color: #fff;
  font-size: 1.2rem;
  line-height: 1.6rem;
  color: #333;
}

.container {
  padding: 2rem;
  &_x {
    counter-reset: faqCounter;
    padding: 0 2rem;
  }
  &_no {
    &-top {
      padding: 0 2rem 2rem 2rem;
    }
  }
}

.title {
  font-size: 1.4rem;
  font-weight: 700;
  line-height: 1.65rem;
  color: #141414;
}

.btn {
  display: block;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  background-color: transparent;
  border: 1px solid transparent;
  padding: 0.375rem 0.75rem;
  font-size: 1.4rem;
  border-radius: 0.25rem;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  margin: 1rem auto;
  width: 100%;
  &-primary {
    color: #fff;
    background-color: #000;
    border-color: #000;
  }
  &-link {
    color: #e1b100;
    background: linear-gradient(90deg, #e1b100 50%, transparent 50%) left bottom /
      10px 1px repeat-x;
    padding: 0 0 3px 0;
    border: none;
    &_left {
      width: fit-content;
      margin: 0 0 20px 0;
      text-align: left;
    }
    &_blue {
      color: #2200a9;
      background: linear-gradient(90deg, #2200a9 50%, transparent 50%) left
        bottom / 10px 1px repeat-x;
      padding-bottom: 3px;
    }
  }
  &-secondary {
    border-color: #b1b1b1;
  }
  &_fit {
    display: inline;
  }
  &-disabled {
    pointer-events: none;
    opacity: 0.65;
  }
}

.detail {
  &-text {
    display: grid;
    gap: 2rem 0;
    margin-bottom: 2rem;
  }
}

.felicitation {
  &__input {
    border-radius: 1rem;
    height: 195px;
    outline: none;
    padding: 17px 25px 1rem 25px;
    width: 100%;
    background-color: #f4f4f4;
    border: none;
    display: grid;
    &-result {
      background-color: #ed4457;
      color: black;
      padding: 1rem;
      flex-direction: column;
      gap: 1rem;
      border-radius: 1rem;
      &__header {
        font-weight: bold;
      }
      &__footer {
        font-size: x-small;
      }
    }
  }
}

.faq {
  &__question {
    margin-bottom: 1.5rem;
    padding: 0;
    border: none;
    &_counter {
      &::before {
        counter-increment: faqCounter;
        content: counter(faqCounter);
        margin-right: 0.7rem;
        display: inline-block;
      }
    }
  }
  &__item {
    margin: 0;
    font-size: 1.4rem;
    &[open] {
      padding-bottom: 1.6rem;
    }
  }
}

.checkbox {
  display: flex;
  align-items: center;
  font-size: 1.2rem;
  button {
    content: "";
    display: inline-block;
    width: 1.5rem;
    height: 1.5rem;
    flex-shrink: 0;
    flex-grow: 0;
    border: 1px solid #b1b1b1;
    border-radius: 3px;
    margin-right: 1rem;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50% 50%;
    background-color: transparent;
    position: relative;
    &:focus {
      border-color: #ffe600;
    }
    img {
      position: absolute;
      left: 0.25rem;
      top: 0.25rem;
    }
  }
  .buttonActive {
    border-color: #ffd600;
    background: no-repeat center, linear-gradient(180deg, #ffe600, #ffd600);
  }
  &__mini {
    font-size: 1.2rem;
  }
  &__read {
    &-all {
      position: sticky;
      bottom: 0;
      padding: 1rem 1.5rem 0.5rem;
      background: #fff;
    }
  }
}
