.program {
  &Item {
    background: #f4f4f4;
    margin-bottom: 20px;
    overflow: hidden;
    &Top {
      position: sticky;
      top: 0;
      background-color: #f4f4f4;
      display: grid;
      gap: 10px;
      z-index: 10;
      padding: 20px 0;
    }
    &Title {
      font-weight: 700;
      font-size: 14px;
      line-height: 16.5px;
    }
  }
  &DetailBtns {
    display: flex;
    justify-content: space-between;
  }
  &Btn {
    font-family: "Roboto", sans-serif;
    display: block;
    font-weight: 400;
    line-height: 1.5;
    color: #212529;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    background-color: transparent;
    border: 1px solid transparent;
    padding: 3.75px 7.5px;
    font-size: 14px;
    border-radius: 2.5px;
    transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
      border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
    margin: 10px auto;
    cursor: pointer;
  }
  &BtnPrimary {
    color: #fff;
    background-color: #000;
    border-color: #000;
    width: 100%;
  }
  &DetailBtn {
    color: #e1b100;
    background: linear-gradient(90deg, #e1b100 50%, transparent 50%) left bottom /
      10px 1px repeat-x;
    padding: 0 0 3px 0;
    border: none;
    width: fit-content;
    margin: 0 0 20px 0;
    text-align: left;
  }
}

.detailText {
  display: grid;
  gap: 20px 0;
  margin-bottom: 20px;
  line-height: 16px;
}

.container {
  padding: 0 20px 20px;
}
.pictures {
  margin-bottom: 20px;
  div {
    position: relative;
    div {
      width: 350px;
      div {
        display: flex;
        div {
          margin: 0 20px;
          height: 280px;
          width: 280px;
          div {
            margin: 0;
          }
        }
        img {
          object-fit: cover;
        }
        video {
          min-height: 50px;
          max-width: 100%;
          max-height: 350px;
          margin-left: auto;
          margin-right: auto;
          display: block;
          &:active {
            background-color: #000;
          }
        }
      }
    }
  }
}

.slickNext,
.slickPrev {
  font-size: 0;
  line-height: 0;
  position: absolute;
  top: 50%;
  display: block;
  width: 20px;
  height: 20px;
  padding: 0;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  cursor: pointer;
  color: transparent;
  border: none;
  outline: 0;
  background: 0 0;
  background-color: rgba(#fff, 0.6);
  border-radius: 50%;
  -webkit-appearance: button;
  overflow: visible;
  z-index: 10;
  transition: background-color 0.3s ease-in-out, width 0.5s ease-in-out,
    height 0.5s ease-in-out;
  &:hover {
    background-color: rgba(#fff, 0.8);
    width: 25px;
    height: 25px;
    &::before {
      font-size: 30px;
      line-height: 10px;
    }
  }
  &::before {
    position: relative;
    font-family: monospace;
    font-size: 22px;
    line-height: 10px;
    top: 0;
    opacity: 0.75;
    color: #000;
    -webkit-font-smoothing: antialiased;
    transition: font-size 0.4s ease-in-out, line-height 0.3s ease-in-out;
  }
}
.slickPrev {
  left: 10px;
  z-index: 2;

  &::before {
    content: "←";
  }
}
.slickNext {
  right: 10px;
  z-index: 2;
  &::before {
    content: "→";
  }
}
