.widgetModal {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  overflow-y: auto;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
  &Inner {
    position: fixed;
    max-width: 100%;
    z-index: 1001;
    background-color: #fff;
    border-radius: 3px;
    box-shadow: 0 0 6px 2px rgba(50, 50, 50, 0.3);
    bottom: 5%;
    right: 5%;
    width: 340px;
  }
  &Frame {
    min-width: 100%;
    max-width: 100%;
    min-height: 90vh;
  }
}

.popupInner {
  height: 90vh;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr auto;
  grid-template-areas:
    "popupTop"
    "popupContent"
    "popupBottom";
}

.popupContent {
  grid-area: popupContent;
  overflow: overlay;
}
