.containerNoTop {
  padding: 0 2rem 2rem 2rem;
}
.variant {
  &List {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  &Image {
    text-align: center;
    img {
      width: 90%;
      border-radius: 5px;
      margin-bottom: 5px;
    }
  }
  &Item {
    margin-bottom: 1rem;
    font-size: 1.4rem;
    line-height: 1.65rem;
    width: 100%;
  }
  &Input {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    margin: 1rem 0;
  }
  &Title{
    font-weight: 700;
  }
}