.map {
  &__container {
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    max-width: 340px;
    overflow: hidden;
    div {
      // max-width: inherit;
      // min-height: 100%;
      .placemark {
        position: absolute;
        bottom: 0;
        z-index: 1;
        padding: 1px;
        border-radius: 10px;
        box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.15),
          0 5px 15px -7px rgba(0, 0, 0, 0.5);
        min-width: none;
        min-height: none;
        width: max-content;
        left: -4.5rem;
        &__inner {
          position: relative;
          display: block;
          background: #fff;
          overflow: hidden;
          border-radius: 10px;
        }
        &__container {
          position: relative;
          z-index: 1;
          display: block;
          min-width: 60px;
          height: 100%;
          min-height: 15px;
          margin: 0;
          padding: 10px 12px;
          background: #fff;
          font: 13px/20px Arial, sans-serif;
        }
        &__content {
          overflow: hidden;
          height: 163px;
          width: 250px;
          display: block;
        }
        &__top {
          display: block;
          margin: 0 0 5px;
          font-size: 120%;
          font-weight: 700;
        }
        &__title {
          font-size: 14px;
          line-height: 16.5px;
          font-weight: 400;
          color: #000;
          margin: 10px;
          text-align: center;
          max-width: 230px;
        }
        &__bottom {
          padding-top: 10px;
        }
        &--img {
          background-position: 0 0;
          height: 6.4rem;
        }
        &__tail {
          display: block;
          position: relative;
          left: 4.5rem;
          width: 17px;
          height: 17px;
          margin-bottom: -17px;
          background: -webkit-linear-gradient(
            45deg,
            rgba(0, 0, 0, 0.15) 50%,
            rgba(0, 0, 0, 0) 50%
          );
          background: linear-gradient(
            45deg,
            rgba(0, 0, 0, 0.15) 50%,
            rgba(0, 0, 0, 0) 50%
          );
          box-shadow: inset 0 0 1px -2px rgba(0, 0, 0, 0.15),
            3px -3px 13px 1px rgba(0, 0, 0, 0.2);
          -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
          -webkit-transform-origin: top left;
          transform-origin: top left;
        }
        &__tail::after {
          content: "";
          position: absolute;
          width: 20px;
          height: 20px;
          background-color: #fff;
          bottom: 1px;
          left: 1px;
        }
      }
    }
  }
}

