.input-group {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  justify-content: flex-end;
  height: 4.5rem;
  align-items: center;
}

.input-group button {
  border: none;
  min-width: 3.5rem !important;
  max-width: 4rem;
  outline: none;
  background-color: #ffe600;
  height: 2.3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.form-control {
  font-size: 1.4rem;
  margin: 0 0.5rem;
  width: 3rem;
  border: none;
  outline: none;
  padding: 0;
  text-align: center;
}
