.popup {
  &Top {
    grid-area: popupTop;
    &Transparent {
      background: transparent;
      z-index: 10;
      height: 0px;
    }
  }
  &Action {
    display: flex;
    justify-content: space-between;
    flex-wrap: nowrap;
    flex-direction: row;
    align-items: center;
    width: 100%;
    height: 23px;
  }
  &Title {
    font-family: "Roboto", sans-serif;
    font-weight: 700;
    font-size: 14px;
    line-height: 16.5px;
    color: #141414;
    text-align: center;
    margin: 10px auto;
  }
  &Back,
  &Close {
    width: 35px;
    height: 35px;
    display: flex;
    align-items: center;
    cursor: pointer;
    background-color: transparent;
    border: none;
  }
  &StepList {
    display: grid;
    grid-auto-flow: column;
    gap: 20px;
  }
  &StepItem {
    height: 11px;
    width: 11px;
    border: 1px solid #b1b1b1;
    box-sizing: border-box;
    border-radius: 50%;
    &Active {
      background-color: #b1b1b1;
    }
  }
  &Close {
    justify-content: flex-end;
  }
}

.container {
  padding: 20px;
}
